<template>
  <div class="wx-register">
    <!-- Header -->
    <TheHeader title=""></TheHeader>

    <div class="wx-registerContent">
      <!-- Banner -->
      <Banner style="height: auto;">
        <img src="./image/banner.png"
             style="width: 100%"
             slot="bannerImg" />
      </banner>

      <RegisterForm>
        <i-field label=""
                 placeholder=""
                 v-model="registerForm.name"></i-field>
        <i-radio align="right"
                 class="page-part"
                 title=""
                 v-model="registerForm.sex"
                 :options="sexOptions" />
         <i-field label=""
               placeholder=""
               type="button"
               class="wx-age is-age wx-requiredInput"
               v-model="registerForm.birthday"
               @click.native="openPicker('picker')"></i-field>
        <i-field label=""
                 placeholder=""
                 v-model="registerForm.phone"></i-field>
        <i-field label=""
                 placeholder=""
                 class="wx-validCode is-valid"
                 v-model="registerForm.verificationCode"
                 v-if="$route.query.bid !== '51'">
          <i-button type="gray"
                    class="wx-validBtn is-valid"
                    @click="onSendMsgCodeRegister()">{{!registerHasSend ? '' : '(' + registerCodeTime + ')'}}</i-button>
        </i-field>

        <div class="btn-group"
             style="padding: 0 15px;">
          <IButton style="margin-top: 30px;"
                   size="large"
                   type="red"
                   @click="submitDataRegister()"></IButton>
          <!-- <IButton style="margin-top: 10px;"
                   size="large"
                   type="gray"
                   @click="$router.push({ name: 'memberBindingCard' })"></IButton> -->
        </div>
      </RegisterForm>
    </div>
     <datetime-picker ref="picker"
                     type="date"
                     v-model="currentDate"
                     @touchmove.native.prevent
                     :startDate="new Date('1900-01-01')"
                     :endDate="new Date()"
                     @confirm="handleChange">
    </datetime-picker>
  </div>
</template>

<script>
import {
  IHeader,
  Banner,
  IField,
  IButton,
  IRadio,
  TheHeader,
} from '@/components/index.js';
import { DatetimePicker } from 'mint-ui';
import RegisterForm from './RegisterForm';
import * as service from '@/api/service';
import moment from 'moment';
import _ from 'lodash';

export default {
  name: 'memberRegister',

  components: {
    IHeader,
    Banner,
    IField,
    IButton,
    IRadio,
    TheHeader,
    DatetimePicker,
    RegisterForm,
  },

  data() {
    return {
      currentDate: new Date(),
      logoPath: '',
      registerForm: {
        name: '',
        sex: '',
        phone: '',
        verificationCode: '',
        birthday: '',
      },
      registerHasSend: false, // 
      registerCodeTime: 60, // 
    };
  },

  created() {
    this.sexOptions = ['', ''];
    this.getMallInfo();
  },

  methods: {
    // logo
    async getMallInfo() {
      const data = await service.getIndex({});
      this.logoPath = `http://woxfile.oss-cn-shenzhen.aliyuncs.com/uploadfile/article/${data.logo}`;
      console.log('logoPath', this.logoPath);
    },
    // 
     openPicker() {
      console.log('openPicker');
      this.$refs.picker.open('picker');
    },
    handleChange(value) {
      this.registerForm.birthday = moment(value).format('YYYY/MM/DD');
    },
    //  - 
    async onSendMsgCodeRegister() {
      // 
      if (this.registerHasSend) {
        return;
      }

      const regexPhone = /^1[3456789]\d{9}$/; // 

      if (!this.registerForm.phone) {
        this.$Toast('');
        return;
      } else if (!regexPhone.test(this.registerForm.phone)) {
        console.log('phone:', this.registerForm.phone);
        this.$Toast('');
        return;
      }

      this.registerHasSend = true;

      console.log('countDownRegister');
      this.countDownRegister();

      const data = await service.sendMsgCode({
        tel: this.registerForm.phone,
      });

      // errCode === -1
      if (data.errCode === -1) {
        this.$Toast(data.errMsg);
      }
    },

    //  - 
    submitDataRegister: _.debounce(
      async function submitDataRegister() {
        const regexName = /^[\u4e00-\u9fa5a-zA-Z]+$/; // 
        const regexPhone = /^1[3456789]\d{9}$/; // 
        const verificationCode = /^[0-9]+$/; // 
        // console.log("",this.registerForm.birthday)
        // const year = moment(this.registerForm.birthday).format('YYYY')  //
        // const mouth = moment(this.registerForm.birthday).format('MM')  //
        // const day = moment(this.registerForm.birthday).format('DD')  //
        // const str1 = this.registerForm.birthday
        // const str = str1.replace(/\//g,"-");   // 2018-12-02

        // console.log("",str)
        console.log("1",this.registerForm.birthday)

        // console.log("",year)
        // console.log("",mouth)
        // console.log("",day)
        if (!this.registerForm.name) {
          this.$Toast('');
          return;
        } else if (!regexName.test(this.registerForm.name)) {
          this.$Toast('');
          return;
        }

        if (!this.registerForm.birthday) {
        this.$Toast('');
        return;
      }

        if (!this.registerForm.phone) {
          this.$Toast('');
          return;
        } else if (!regexPhone.test(this.registerForm.phone)) {
          this.$Toast('');
          return;
        }

        if (this.$route.query.bid !== '51') {
          if (!this.registerForm.verificationCode) {
            this.$Toast('');
            return;
          } else if (
            !verificationCode.test(this.registerForm.verificationCode)
          ) {
            this.$Toast('');
            return;
          }

          // if (!this.hasAgreeProto) {
          //   this.$Toast('');
          //   return;
          // }
        }
        // this.registerForm.birthday = userInfo.birthday !== null ? moment(userInfo.birthday * 1000).format('YYYY/MM/DD') : null;
        const data = await service.getWeixinCards({
          submit: 1,
          verify: this.registerForm.verificationCode,
          userTrueName: this.registerForm.name,
          userPhone: this.registerForm.phone,
          identityCard: this.registerForm.id,
          birthday:  this.registerForm.birthday
        });

        this.formDisabled = false;

        if (data.errCode === 0) {
          this.$MessageBox({
            title: '',
            message: data.errMsg,
            closeOnClickModal: false,
          }).then(() => {
            if (this.$route.query.callback_url) {
              window.location.href = this.$route.query.callback_url;
              return;
            }

            this.$router.replace({ name: 'memberCenter' });
          });
        } else {
          this.$Toast(data.errMsg);
        }
      },
      1000,
      {
        leading: true,
        trailing: false,
      },
    ),

    //  - 
    countDownRegister() {
      setTimeout(() => {
        this.registerCodeTime -= 1;

        if (this.registerCodeTime <= 0) {
          this.registerCodeTime = 60;
          this.registerHasSend = false;
          return;
        }
        this.countDownRegister();
      }, 1000);
    },
  },
};
</script>

<style lang="scss">
@include b(register) {
  min-height: 100vh;
  background: #ededed;

  /*  */
  .i-field-core {
    border-radius: rem(3);
  }
  .i-cell-wrapper {
    background: none;
  }
}

@include b(registerContent) {
  padding: rem(20);
}

@include b(validBtn) {
  display: flex;
  align-items: center;

  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: rem(240);

  color: #323232;

  @include when(valid) {
    font-size: rem(24);
    height: rem(70);
  }
}

@include b(validCode) {
  @include when(valid) {
    .i-field-core {
      // width: rem(445);
      // flex: 0;
      flex-flow: 1;
      margin-right: rem(30);
    }
    .i-cell-value {
      display: flex;
      justify-content: space-between;
    }
  }
}
</style>





